import React, { Component } from 'react'
import { MyContext } from '../../MyProvider'
import Carousel from './components/carousel'

class HomePage extends Component {
  state={
    titles: [
      ["DONUTS", "donuts"],
      ["POKEDEX", "pokedex"],
      ["To-Do List", 'todolist'],
    ],
    history: 1000,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ history: 0 })
    })
  }

  handleUnmount = () => {
    const { history } = this.props
    const { titles } = this.state
    const { context } = this
    this.setState({ history: 1000 })
    setTimeout(() => {
      history.push(`/projects/${titles[context.state.current][1]}`)
    }, 200)
  }

  render() {
    const { history, titles } = this.state

    const hStyle = {
      transform: `translate(-50%, ${history}%)`,
    }
    const nextStyle = {
      transform: `translate(${history}%, 0%)`,
    }
    const prevStyle = {
      transform: `translate(-${history}%, 0%)`,
    }
    return (
      <MyContext.Consumer>
        {context => (
          <div className="HomePage">
            <div className="pseudoBackground zoom" style={{ backgroundImage: `url(${context.state.items[context.state.current]})` }} />
            <button
              type="button"
              onClick={this.handleUnmount}
              className="title cursorPointer"
            >
              {titles[context.state.current][0]}
            </button>

            <Carousel
              current={context.state.current}
              items={context.state.items}
              changeSlide={context.setIndex}
              hStyle={hStyle}
              prevStyle={prevStyle}
              nextStyle={nextStyle}
              handlerPrev={context.handlerPrev}
              handlerNext={context.handlerNext}
            />
          </div>
        )}
      </MyContext.Consumer>
    );
  }
}

export default HomePage

HomePage.contextType = MyContext
