import React, { Component } from 'react'
import { MyContext } from '../../../MyProvider'
import Presentation from './projectTemplate/presentation'
import Stills from './projectTemplate/stills'
import Button from './projectTemplate/button'
import ExitButton from './projectTemplate/exitbutton'
import ProjectBackground from './projectTemplate/projectbackground'
import oops from '../../../img/oops.jpg'

class ProjectTemplates extends Component {
  // scrollAmt = 0.6;

  constructor(props) {
    super(props);
    this.state = {
      minHeight: "100vh",
      XButtonPos: -1000,
    }
    this.parallaxBack = React.createRef();
  }

  componentWillMount = () => {
    // document.addEventListener('scroll', this.onScroll, false)
    document.body.classList.remove('overflowHidden');
  }

  componentDidMount() {
    this.enterAnimation()
  }

  componentWillUnmount = () => {
    // document.removeEventListener('scroll', this.onScroll, false)
  }

  handleUnmount= () => {
    this.exitAnimation()
  }

  // onScroll = () => {
  //   const offset = window.pageYOffset;
  //   this.parallaxBack.current.style.backgroundPositionY = `${-offset * this.scrollAmt}px`;
  // }

  enterAnimation() {
    setTimeout(() => {
      this.setState({
        minHeight: "90vh",
        XButtonPos: -50,
      })
    })
  }

  exitAnimation() {
    const { context } = this
    const { index, router } = this.props
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    })

    this.setState({
      minHeight: '100vh',
      XButtonPos: -1000,
    });

    document.body.classList.add('overflowHidden');
    context.setIndex(0, index)
    setTimeout(() => {
      router.history.push(`/`)
    }, 1000)
  }

  render() {
    const { back, title, presentationText, itemsArray, color, githubLink, hostLink, githubButtonText, hostButtonText } = this.props
    const { minHeight, XButtonPos } = this.state
    const { handleUnmount, goToHistoryClick, parallaxBack } = this
    const bgStyle = {
      backgroundImage: `url(${back})`,
      minHeight: `${minHeight}`
    }
    const Xstyle = {
      transform: `translate(-50%, ${XButtonPos}%)`,
    }
    return (
      <div className="project-scroll">
        <ExitButton
          handleUnmount={handleUnmount}
          Xstyle={Xstyle}
        />
        <ProjectBackground
          parallaxBack={parallaxBack}
          bgStyle={bgStyle}
          title={title}
        />
        <Presentation presentationText={presentationText} />
        <Stills
          items={itemsArray}
          changeSilde={goToHistoryClick}
          color={color}
        />
        <section className="viewOnline">
          {githubLink
          && (<Button
            link={githubLink}
            buttonText={githubButtonText}
          />)}
          {hostLink && <Button
            link={hostLink}
            buttonText={hostButtonText}
          />}
        </section>
      </div>
    );
  }
}

ProjectTemplates.defaultProps = {
  title: 'Project Name',
  presentationText: 'Project description',
  back: { oops },
  itemsArray: [oops],
  color: "#000",
};

export default ProjectTemplates
ProjectTemplates.contextType = MyContext
