import React from 'react'
import History from '../../shared/history'

const Carousel = ({ current, items, changeSlide, hStyle, prevStyle, nextStyle, handlerPrev, handlerNext }) => (
  <section className="carousel">
    <button type="button" className="carousel_control carousel_control__prev" style={prevStyle} onClick={handlerPrev}><span /></button>
    <button type="button" className="carousel_control carousel_control__next" style={nextStyle} onClick={handlerNext}><span /></button>
    <div className="carousel_history" style={hStyle}>
      <History
        current={current}
        items={items}
        changeSlide={changeSlide}
      />
    </div>
  </section>
)

export default Carousel
