import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import SideNav from './components/sidenav'


class Nav extends Component {
  handleSlideMenu = (param) => () => {
    this.sideNav.style.transform = `translateX(${param}%)`
  }

  render() {
    return (
      <>
        <div className="Nav">
          <Link to="/"><h1 className="nameLink">TONY HO</h1></Link>
          <button
            type="button"
            className="aboutLink"
            onClick={this.handleSlideMenu(0)}
          >
            about
          </button>
        </div>
        <div ref={sideNav => { this.sideNav = sideNav }} id="sideNav">
          <button
            type="button"
            className="XButton"
            onClick={this.handleSlideMenu(-100)}
          >
            X
          </button>

          <SideNav />
        </div>
      </>
    );
  }
}

export default Nav
