import React, { Component } from 'react'
import History from '../../../shared/history'

class Stills extends Component {
  state = {
    current: 0,
  }

  handlerPrev = () => {
    let { current } = this.state
    const { items } = this.props

    if(current < 1) {
      current = items.length
    }
    current -= 1
    this.setState({ current })
  }

  handlerNext= () => {
    let { current } = this.state
    const { items } = this.props

    if(current === items.length - 1) {
      current = -1
    }
    current += 1
    this.setState({ current })
  }


  goToHistoryClick = (curIndex, index) => {
    this.setState({
      current: index,
    })
  }

  render() {
    const { color, items, index } = this.props
    const { current } = this.state
    const bgColor = { backgroundColor: `${color}` }
    return(
      <section className="stills" style={bgColor}>
        <div className="carouselProject">
          <div className="carouselProject_slide" key={index}>
            <img src={items[current]} alt="stills of some awesome website" />
          </div>
          <button
            className="carouselProject_control carousel_control__prev"
            onClick={this.handlerPrev}
            type="button"
          >
            <span />
          </button>
          <button
            className="carouselProject_control carousel_control__next"
            onClick={this.handlerNext}
            type="button"
          >
            <span />
          </button>
          <div className="carouselProject_history projectHistory">
            <History
              current={current}
              items={items}
              changeSilde={this.goToHistoryClick}
            />
          </div>
        </div>
      </section>
    )
  }
}

Stills.defaultProps = {
  items: [],
  color: "#fff",
  changeSilde: () => null,
};

export default Stills
