import React from 'react'

const MainTitle = ({ title, classname, handleUnmount }) => (
  <button
    type="button"
    className={classname}
    onClick={handleUnmount}
  >
    {title}
  </button>
)

MainTitle.defaultProps = {
  onClick: () => null,
  classname: "title no-cursor",
  title: "Project Title",
}

export default MainTitle
