import React, { Component } from 'react';
import donutBackground from "./img/donut-background.jpg"
import pokeBackground from "./img/pokemon-background.jpg"
import todoBackground from "./img/todo-background.jpg"

export const MyContext = React.createContext();
export class MyProvider extends Component {
  state = {
    items: [
      donutBackground,
      pokeBackground,
      todoBackground,
    ],
    current: 0,
  }
  render() {
    const { children } = this.props
    return (
      <MyContext.Provider value={{
        state: this.state,
        handlerPrev: () => {
          let { current, items } = this.state
          if(current < 1) {
            current = items.length
          }
          current -= 1;

          this.setState({ current });
        },
        handlerNext: () => {
          let { current, items } = this.state
          if(current === items.length - 1) {
            current = -1;
          }
          current += 1;

          this.setState({ current });
        },
        setIndex: (curIndex, index) => {
          this.setState({
            current: index,
          });
        },
      }}
      >
        {children}
      </MyContext.Provider>
    )
  }
}
