import React, { Component } from 'react'
import { HashRouter as Router, Route, Switch } from 'react-router-dom'
import { MyProvider } from './MyProvider'
import HomePage from './containers/home/HomePage'
import Donuts from './containers/projects/Donuts'
import Pokedex from './containers/projects/Pokedex'
import ToDoList from './containers/projects/ToDoList'
import Nav from './containers/nav/Nav'


class App extends Component {
  state = {
    opacity: 0,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ opacity: 1 })
    }, 0.5)
  }

  render() {
    const { opacity } = this.state
    return (
      <Router>
        <MyProvider>
          <div className="App" style={{ opacity: `${opacity}` }}>
            <Nav />
            <Switch>
              <Route exact path="/" render={props => <HomePage {...props} />} />
              <Route exact path="/projects/donuts" render={props => <Donuts {...props} />} />
              <Route exact path="/projects/pokedex" render={props => <Pokedex {...props} />} />
              <Route exact path="/projects/todolist" render={props => <ToDoList {...props} />} />
            </Switch>
          </div>
        </MyProvider>
      </Router>
    )
  }
}

export default App
